import React, { useState, useEffect } from 'react';
import './App.css';
import * as XLSX from 'xlsx';
import html2canvas from 'html2canvas';


function App() {
  const rows = 18; // The maximum number of rows
  const cols = 34;

  // Get the current month and year
  const currentMonth = new Date().toLocaleString('default', { month: 'long' });
  const currentYear = new Date().getFullYear();

  // Get the total number of days in the current month
  const daysInMonth = new Date(currentYear, new Date().getMonth() + 1, 0).getDate();

  // Generate the days of the month dynamically
  const generateDays = () => {
    return Array.from({ length: daysInMonth }, (_, i) => i + 1);
  };

  // Generate the weeks based on the number of days
  const generateWeeks = () => {
    const days = generateDays();
    const weeks = [];
    let week = [];

    days.forEach((day, index) => {
      week.push(day);
      if (week.length === 7 || index === days.length - 1) {
        weeks.push(week);
        week = [];
      }
    });

    return weeks;
  };

  const weeks = generateWeeks();

  // Function to determine if a day is a Sunday
  const isSunday = (day) => {
    const date = new Date(currentYear, new Date().getMonth(), day);
    return date.getDay() === 0; // Returns true if it's Sunday
  };

  // Create an array of Sunday column indexes
  const sundayColumns = weeks.flat().map((day, index) => isSunday(day) ? index : -1).filter(index => index !== -1);

  // State to manage the visibility of the popup form
  const [isPopupVisible, setIsPopupVisible] = useState(false);

  // State to manage form data and table rows
  const [formData, setFormData] = useState({
    emri: '',
    mbiemri: '',
  });

  // State for the input field data (number, X, etc.)
  const [inputData, setInputData] = useState(JSON.parse(localStorage.getItem('inputData')) || {});

  // State to manage the notification visibility
  const [notification, setNotification] = useState(false);

  // Retrieve employees from local storage
  const getEmployeesFromLocalStorage = () => {
    const storedEmployees = JSON.parse(localStorage.getItem('employees')) || [];
    return storedEmployees;
  };

  const [employees, setEmployees] = useState(getEmployeesFromLocalStorage());

  // Handle form changes
  const handleFormChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  // Function to handle form submission
  const handleFormSubmit = (e) => {
    e.preventDefault();
    const newEmployee = {
      emri: formData.emri,
      mbiemri: formData.mbiemri,
    };

    // Save the new employee to localStorage
    const updatedEmployees = [...employees, newEmployee];
    setEmployees(updatedEmployees);
    localStorage.setItem('employees', JSON.stringify(updatedEmployees));

    // Clear the form after submitting
    setFormData({
      emri: '',
      mbiemri: '',
    });

    // Close the popup
    setIsPopupVisible(false);
  };

  // Toggle popup visibility
  const togglePopup = () => {
    setIsPopupVisible(!isPopupVisible);
  };

  // Handle input field change (number, X, etc.) for specific row/column
  const handleInputChange = (rowIndex, day, value) => {
    const newInputData = { ...inputData };

    // Modify the input field value for the specific row and day
    if (!newInputData[rowIndex]) {
      newInputData[rowIndex] = {};
    }

    // Ensure that the value is either 'X' or a number between 1-9
    if (value === 'X' || (/^[1-9]$/.test(value))) {
      newInputData[rowIndex][day] = value;
    }

    setInputData(newInputData);
    localStorage.setItem('inputData', JSON.stringify(newInputData));
  };

  // Save all the data to local storage
  const saveData = () => {
    localStorage.setItem('inputData', JSON.stringify(inputData));

    // Display success notification
    setNotification(true);

    // Hide notification after 3 seconds
    setTimeout(() => {
      setNotification(false);
    }, 3000);
  };
  const downloadTableAsExcel = () => {
    // Define the current month and year for the header
    const monthHeader = `Month: ${currentMonth} ${currentYear}`;

    // Generate the weeks dynamically. Assume `weeks` is an array of week objects, where each week has days.
    const weekHeaders = weeks.map((week, index) => `Week ${index + 1}`);
    const header = [
      [monthHeader],  // First row: Month header
      ['Nr', 'Emri', 'Mbiemri', ...weekHeaders],  // Second row: Week headers
    ];

    // Create the Excel worksheet with the header
    const ws = XLSX.utils.aoa_to_sheet(header);

    // Mark the Sundays with green color
    const sundayStyle = { fill: { fgColor: { rgb: '90EE90' } } }; // Light green background

    // Loop through each employee and generate their row data
    employees.forEach((employee, rowIndex) => {
      const row = [
        rowIndex + 1,  // Employee number
        employee.emri,
        employee.mbiemri,
        ...weeks.flat().map((day, colIndex) => {
          // Get the input value for each day
          const value = inputData[rowIndex]?.[day] || '';

          // Check if this is a Sunday (and style it)
          if (isSunday(day)) {  // Assuming `isSunday` is a function to check if it's Sunday
            const cell = { v: value, s: sundayStyle }; // Apply the Sunday style
            return cell;
          }

          return value; // No styling for non-Sunday days
        }),
      ];

      // Add the employee row to the sheet
      XLSX.utils.sheet_add_aoa(ws, [row], { origin: -1 });
    });

    // Create the workbook
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

    // Download the workbook as an Excel file
    XLSX.writeFile(wb, 'employee_table_with_input.xlsx');
  };


  function saveTableAsScreenshot() {
    const table = document.querySelector('table'); // Target your table

    html2canvas(table).then((canvas) => {
      const link = document.createElement('a');
      link.href = canvas.toDataURL('image/png');  // Create a data URL for the image
      link.download = 'table_screenshot.png';  // Specify the file name
      link.click();
    });
  }


  return (
    <div className="App">
      {/* Notification at the top-right */}
      {notification && (
        <div className="notification">
          <p>Oret jane ruajtur me sukses!</p>
        </div>
      )}

      <table id="employee-table">
        <thead>
          {/* First header row with the Month and titles */}
          <tr className="first-row">
            <th colSpan="3">{`Month ${currentMonth}`}</th>
            {weeks.map((_, weekIndex) => (
              <th key={weekIndex} colSpan="7">
                {`Week ${weekIndex + 1}`}
              </th>
            ))}
          </tr>

          {/* Second header row with specific column names */}
          <tr>
            <th>Nr</th>
            <th>Emri</th>
            <th>Mbiemri</th>

            {/* Render sub-columns for each day of the weeks */}
            {weeks.map((week, weekIndex) => {
              return week.map((day, dayIndex) => (
                <th
                  key={day}
                  className={isSunday(day) ? 'sunday' : ''}
                >
                  {day}
                </th>
              ));
            })}
          </tr>
        </thead>

        {/* Table body - dynamically based on number of employees */}
        <tbody>
          {employees.map((employee, rowIndex) => (
            <tr key={rowIndex}>
              <td>{rowIndex + 1}</td>
              <td>{employee.emri}</td>
              <td>{employee.mbiemri}</td>
              {weeks.flat().map((day, colIndex) => (
                <td
                  key={colIndex}
                  className={sundayColumns.includes(colIndex) ? 'sunday' : ''}
                >
                  {/* Render an input field for non-Sunday days */}
                  {!isSunday(day) && (
                    <input
                      type="text"
                      value={inputData[rowIndex]?.[day] || ''}
                      onChange={(e) => handleInputChange(rowIndex, day, e.target.value)}
                      className="input-field"
                    />
                  )}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>

      {/* Add button */}
      <button className="add-button" onClick={togglePopup}>Shto punetorin</button>

      {/* Save data button */}
      <button className="save-button" onClick={saveData}>Ruaj oret</button>

      {/* Download table button */}
      <button className="download-button" onClick={saveTableAsScreenshot}>Download Table</button>

      {/* Popup form */}
      {isPopupVisible && (
        <div className="popup-container">
          <div className="popup-form">
            <h2>Shto Punetorin</h2>
            <form onSubmit={handleFormSubmit}>
              <label>Emri:</label>
              <input
                type="text"
                name="emri"
                value={formData.emri}
                onChange={handleFormChange}
              />
              <label>Mbiemri:</label>
              <input
                type="text"
                name="mbiemri"
                value={formData.mbiemri}
                onChange={handleFormChange}
              />
              <button className="ruaj-button" type="submit">Ruaj punetorin</button>
            </form>
            <button className="close-popup" onClick={togglePopup}>Mbyll</button>
          </div>
        </div>
      )}
    </div>
  );
}

export default App;
